import FPSLabel from "./CommonScript/FPSLabel";

const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    particleBundle: cc.AssetManager.Bundle = null;

    onLoad () {
        cc.assetManager.loadBundle("particle", (err, particleBundle)=>{
            if (!err) {
                this.particleBundle = particleBundle;
            }
        })
    }

    loadParticleWithTIFF () {
        console.log(`start emit test time is: ${Date.now()} ms`);
        this.particleBundle && this.particleBundle.load("LeavesWithTIFF", cc.Prefab, (err, particle: cc.Prefab)=>{
            if (!err) {
                var node = cc.instantiate(particle);
                this.node.addChild(node);
            }
        })
    }

    loadParticleWidthPng () {
        console.log(`start emit test time is: ${Date.now()} ms`);
        this.particleBundle && this.particleBundle.load("LeavesWithPng", cc.Prefab, (err, particle: cc.Prefab)=>{
            if (!err) {
                var node = cc.instantiate(particle);
                this.node.addChild(node);
            }
        })
    }
}
